import * as QRCode from "qrcode.react";
import * as React from "react";
import * as CopyToClipboard from "react-copy-to-clipboard";
import CopyIcon from "./images/copy_icon.svg";
import styled from "styled-components";

const OutdivStyle = styled.div`
    font-family: 'Source Code Pro', monospace;
    background-color: #E6DB74;
    padding: 0.1em;
    font-size: 1.75em;
    text-align: center;
    float: center;
    margin: 0 auto;

    /* TODO hover */
    /* .outdiv:hover {
        background-color: #EEDD77;
    } */
`;

const OutdivInnerStyle = styled.div`
    display: inline-flex;

    padding: 0.4em;
`;

const PasswordStyle = styled.input`
    font-family: 'Source Code Pro', monospace;
    font-size: ${props => 19 / Math.max(props.len, 19) + "em"};
    background-color: #E6DB74;
    padding: 0.1em;
    text-align: center;
    float: center;
    margin: 0 auto;
    border-style: dotted;
    border-radius: 0.3em;
    border-color: #d1c666;
    border-width: 0.1em;
`;

const CopyButtonStyle = styled.img`
    margin-left: 0.4em;
    padding: 0.2em;
    width: 0.8em;
    height: 0.8em;
    background-color: rgb(255, 251, 211);

    /* TODO pressed state */
    /* TODO hover state */
`;

const QRCodeContainer = styled.div`
    display: block;
`;

declare class UIOutDivProps {
    public password?: string;
    public generateTip?: string;
}

class State {
    public copied: boolean = false;
}

export class UIOutDiv extends React.Component {
    // @ts-ignore
    public props: UIOutDivProps;
    public state = new State();

    public render(): JSX.Element {
        let inner;
        if (this.props.password) {
            const pass = this.props.password;
            inner = <>
                <PasswordStyle len={pass.length} readOnly value={pass} />
                <CopyToClipboard
                    text={pass}
                    onCopy={() => this.setState({ copied: true })}>
                    <CopyButtonStyle src={CopyIcon} />
                </CopyToClipboard>
            </>;
        }
        else {
            inner = <span>{this.props.generateTip}</span>
        }

        return <OutdivStyle>
            <OutdivInnerStyle>
                {inner}
            </OutdivInnerStyle>
            {this.renderQRCode()}
        </OutdivStyle>;
    }

    private renderQRCode(): JSX.Element {
        if (this.props.password) {
            return <QRCodeContainer><QRCode value={this.props.password} /></QRCodeContainer>;
        }
        return <QRCodeContainer><QRCode value={"invalid"} bgColor="white" fgColor="lightgrey" /></QRCodeContainer >;
    }
}
