import * as React from "react";
import { FormSectionStyle, DescriptionStyle, HintStyle, InputStyle } from ".";

declare class UIPassPhraseInputProps {
    public onPassPhraseSelected: (phrase: string, usable: boolean) => void;
}

class UIPassPhraseInputState {
    public selectedPhrase: string | undefined;
}

const MIN_PASSPHRASE_LENGTH = 5;

export class UIPassPhraseInput extends React.Component {
    // @ts-ignore
    public props: UIPassPhraseInputProps;
    public state = new UIPassPhraseInputState();

    public render() {
        return (<FormSectionStyle>
            <DescriptionStyle>
                Passphrase{this.okText()}<br />
                <HintStyle>{this.hintText()}</HintStyle>
            </DescriptionStyle>
            <InputStyle
                autoComplete="off"
                autoCorrect="off"
                autoCapitalize="off"
                spellCheck={false}
                type="password"
                onChange={this.onChange}
            />
        </FormSectionStyle>);
    }

    private okPhrase(): boolean {
        if (this.state.selectedPhrase) {
            return this.state.selectedPhrase.length >= MIN_PASSPHRASE_LENGTH;
        }
        return false;
    }

    private okText(): string {
        return this.okPhrase() ? " " : "";
    }

    private hintText(): string {
        if (this.state.selectedPhrase) {
            if (this.state.selectedPhrase.length < MIN_PASSPHRASE_LENGTH) {
                return "Too short. At least 5 characters";
            }
        }
        return "(optional) protect your key file";
    }

    private onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const usable = e.target.value.length >= MIN_PASSPHRASE_LENGTH || e.target.value.length === 0;

        this.props.onPassPhraseSelected(e.target.value, usable);
        this.setState({ selectedPhrase: e.target.value });
    }
}
