import * as React from "react";
import { FormSectionStyle, DescriptionStyle, WarningStyle } from "./index";
import styled from "styled-components";

// TODO how to make mixins work so this can be imported from input.tsx?
const FakeInput = styled.button`
    font-size: 1em;
    padding: 0.5em;
    border-radius: 0.3em;
    float: right;
    border-style: dotted;
    border-color: #CCCCCC;
    border-width: 0.1em;
    background-color: #FAFAFA;
    min-width: 14.7em;
    text-align: left;
    /* what the hell is this number? Why is it 1.2 on inputs and 2.6 here?? */
    max-height: 2.5em;

    &:focus {
        background-color: white;
        border-color: #4286f4;
        outline: none;
    }
`;

const InvisiblePicker = styled.input`
    display: none;
`;

declare class UIFileComponentProps {
    public onFilePicked: (name: File | undefined) => void;
}

class UIFileComponentState {
    public selectedFile: File | undefined;
}

export class UIFileComponent extends React.Component {
    // @ts-ignore
    public props: UIFileComponentProps;
    public state = new UIFileComponentState();

    private filePicker = React.createRef<HTMLInputElement>();

    public onClick = () => {
        if (this.filePicker.current) {
            this.filePicker.current.click();
        }
    }

    public onFileSelected = (e: React.ChangeEvent<HTMLInputElement>) => {
        function validateFile(): File | undefined {
            if (!e.target.files || e.target.files.length === 0) {
                return;
            }

            if (e.target.files.length > 1) {
                alert("Select only one file");
                return;
            }
            return e.target.files[0];
        }

        const file = validateFile();

        this.props.onFilePicked(file);
        this.setState({ selectedFile: file });
    }

    public render() {
        return <FormSectionStyle>
            <DescriptionStyle>
                <span>Choose your key file{this.renderOkText()}</span><br />
                <WarningStyle>
                    WARNING: you will lose your passwords losing this file, or changing it in any way!
                </WarningStyle>
            </DescriptionStyle>
            <InvisiblePicker
                type="file"
                ref={this.filePicker}
                onChange={this.onFileSelected}
            />
            <FakeInput onClick={this.onClick}>
                <b>{this.fileFieldContent()}</b>
            </FakeInput>

        </FormSectionStyle>;
    }

    private fileFieldContent(): string {
        // place a bunch of dots in the out field
        if (this.state.selectedFile) {
            return new Array(this.state.selectedFile.name.length + 1).join("");
        }
        return "No file selected";
    }

    private renderOkText(): string {
        return this.state.selectedFile ? " " : "";
    }
}
