import "./styles/main.css";
import * as React from "react";
import * as ReactDOM from "react-dom";
import { UIDomainInput } from "./domain_component";
import { UIFileComponent } from "./file_component";
import "react-toggle/style.css";
import Toggle from "react-toggle";
import { UIPassPhraseInput } from "./passphrase_input";
import { UIOutDiv } from "./outdiv";
import styled from "styled-components";

const ContainerStyle = styled.div`
    margin: 0 auto;
    max-width: 35em;
    border-style: solid;
    border-width: 1px;
    border-color: #EEEEEE;
    padding: 0.5em;
    background-color: #FDFDFA;
`;

export const FormSectionStyle = styled.div`
    display: flex;
    flex-wrap: wrap;
    padding: 0.75em;
`;

export const DescriptionStyle = styled.div`
    flex-grow: 100;
    width: 50%;
    height: 2.5em;
`;

const CommitHashStyle = styled.span`
    font-size: 0.85em;
    color: grey;
    font-style: italic;
`;

export const ToggleContainerStyle = styled.div`
    min-width: 14.2em;
    padding: 0.25em;
    align-items: baseline;
`;

export const NoticeStyle = styled.span`
    font-size: 0.82em;
    padding-left: 1em;
`;

export const WarningStyle = styled(NoticeStyle)`
    color: #CC8888;
    max-width: 45%;
    padding: 1em;
`;

export const HintStyle = styled(NoticeStyle)`
    font-style: italic;
    color: #888888;
`;

export const InputStyle = styled.input`
    font-size: 1em;
    padding: 0.5em;
    border-radius: 0.3em;
    float: right;
    border-style: dotted;
    border-color: #CCCCCC;
    border-width: 0.1em;
    background-color: #FAFAFA;
    min-width: 13.5em;
    max-height: 1.3em;

    &:focus {
        background-color: white;
        border-color: #4286f4;
        outline: none;
    }
`;

declare const COMMITHASH: string;

function isCorrectCharset(
    password: string,
    includeLowercase: boolean,
    includeNumbers: boolean,
    includeSymbols: boolean,
    includeUppercase: boolean) {

    let hasNumber = false;
    let hasSymbol = false;
    let hasUppercase = false;
    let hasLowercase = false;

    for (let i = password.length - 1; i >= 0; i--) {
        const c = password[i];

        if (c >= "a" && c <= "z") {
            hasLowercase = true;
        }
        else if (c >= "A" && c <= "Z") {
            hasUppercase = true;
        }
        else if (c >= "0" && c <= "9") {
            hasNumber = true;
        }
        else {
            hasSymbol = true;
        }
    }

    return (
        hasLowercase === includeLowercase &&
        hasNumber === includeNumbers &&
        hasSymbol === includeSymbols &&
        hasUppercase === includeUppercase
    );
}

class UIRootState {
    public generateError: string | undefined;
    public password: string | undefined;
}

class UIRoot extends React.Component {
    public state = new UIRootState();

    private file: File | undefined;
    private domain: string | undefined;
    private phrase: string = "";
    private phraseUsable: boolean = true;
    private requireSymbols: boolean = true;
    private passLength?: number = 20;

    public componentWillMount() {
        this.updateInputs();
    }

    public onFilePicked = (name: File | undefined) => {
        this.file = name;
        this.updateInputs();
    }

    public onDomainPicked = (domain: string | undefined) => {
        this.domain = domain;
        this.updateInputs();
    }

    public onPassPhraseSelected = (phrase: string, usable: boolean) => {
        this.phrase = phrase;
        this.phraseUsable = usable;
        this.updateInputs();
    }

    public onSymbolsAllowedUpdated = (e: React.ChangeEvent<HTMLInputElement>) => {
        this.requireSymbols = e.target.checked;
        this.updateInputs();
    }

    public onKeyPress = (e: React.KeyboardEvent) => {
        if (e.key === "Enter" && this.state.generateError === undefined) {
            this.startGeneration();
        }
    }

    public onPasswordLengthChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
        this.passLength = parseInt(e.currentTarget.value, 10);
        this.updateInputs();
    }

    public render() {
        return (
            <ContainerStyle onKeyPress={this.onKeyPress}>
                <h1>Make password, yes?</h1>
                <hr />
                <UIDomainInput onDomainPicked={this.onDomainPicked} />
                <UIFileComponent onFilePicked={this.onFilePicked} />
                <UIPassPhraseInput onPassPhraseSelected={this.onPassPhraseSelected} />
                <FormSectionStyle>
                    <DescriptionStyle>
                        Password length
                    </DescriptionStyle>
                    <InputStyle
                        type="number"
                        defaultValue={this.passLength}
                        onChange={this.onPasswordLengthChanged}
                    />
                </FormSectionStyle>
                <FormSectionStyle>
                    <DescriptionStyle>
                        With symbols (/+)
                    </DescriptionStyle>
                    <ToggleContainerStyle>
                        <Toggle
                            defaultChecked={true}
                            icons={false}
                            onChange={this.onSymbolsAllowedUpdated}
                        />
                    </ToggleContainerStyle>
                </FormSectionStyle>
                <UIOutDiv
                    password={this.state.password}
                    generateTip={this.state.generateError}
                />
                <CommitHashStyle>
                    Version {COMMITHASH} - <a href="old/index.html">go to old version</a>
                </CommitHashStyle>
            </ContainerStyle>);
    }


    private startGeneration = () => {
        if (!this.file || !this.domain) {
            return;
        }

        const reader = new FileReader();

        reader.onload = () => {
            // hack?
            const crypto = window.crypto.subtle;

            let buffer = reader.result as string;

            // append the domain
            buffer += this.domain;
            // append the phrase if any
            if (this.phrase) {
                buffer += this.phrase;
            }

            // this function calls itself until the password fits the requirements
            const generateIteration = () => {
                const raw = new TextEncoder().encode(buffer);

                crypto.digest("SHA-512", raw).then((value: ArrayBuffer) => {
                    const base64String = btoa(String.fromCharCode(...new Uint8Array(value)))
                        .substring(0, this.passLength);

                    // check if we good. If not, run append the current password 
                    // to the end of the blob, and restart
                    if (isCorrectCharset(base64String, true, true, this.requireSymbols, true)) {
                        this.setState({
                            password: base64String,
                        });
                    }
                    else {
                        buffer += base64String;
                        generateIteration();
                    }
                });
            };

            // start the iterations
            generateIteration();
        };

        reader.readAsBinaryString(this.file);
    }

    private getNextMissingField(): string | undefined {
        if (!this.domain) {
            return "Pick a domain";
        }
        if (!this.file) {
            return "Pick a file";
        }
        if (!this.phraseUsable) {
            return "Pick a valid passphrase";
        }
        if (!this.passLength) {
            return "Pick a valid password length";
        }
        return;
    }

    private updateInputs() {
        const generateError = this.getNextMissingField();

        // if nothing is missing, make a password right away
        if (!generateError) {
            this.startGeneration();
        }

        this.setState({
            generateError,
        });
    }
}

ReactDOM.render(
    <UIRoot />,
    document.body.appendChild(document.createElement("div")),
);
